/**
 * Using Rails-like standard naming convention for endpoints.
 * GET     /api/uploads              ->  index
 * POST    /api/uploads              ->  create
 * GET     /api/uploads/:id          ->  show
 * PUT     /api/uploads/:id          ->  update
 * DELETE  /api/uploads/:id          ->  destroy
 */

//'use strict';

import _ from 'lodash';
import Upload from './upload.model';
import fs from 'fs';
/* AWS S3 */
import AWS from 'aws-sdk';
import config from '../../config/environment';
import formidable from 'formidable';
import path from 'path';

function respondWithResult(res, statusCode) {
  statusCode = statusCode || 200;
  return function(entity) {
    if (entity) {
      res.status(statusCode).json(entity);
    }
  };
}

function saveUpdates(updates) {
  return function(entity) {
    var updated = _.merge(entity, updates);
    return updated.save()
      .then(updated => {
        return updated;
      });
  };
}

function removeEntity(res) {
  return function(entity) {
    if (entity) {
      return entity.remove()
        .then(() => {
          res.status(204).end();
        });
    }
  };
}

function handleEntityNotFound(res) {
  return function(entity) {
    if (!entity) {
      res.status(404).end();
      return null;
    }
    return entity;
  };
}

function handleError(res, statusCode) {
  statusCode = statusCode || 500;
  return function(err) {
    res.status(statusCode).send(err);
  };
}

AWS.config.credentials = config.AWS;
//AWS S3 Object
var S3 = new AWS.S3();

// Gets a list of Uploads
export function index(req, res) {
  S3.listObjects({Bucket:'pmgv-files'},(err, data)=>{
    if(err){
        return respondWithResult(res)(err);  
      }
      return respondWithResult(res)(data);
  });

}

// Gets a single Upload from the DB
export function show(req, res) {
  var opt = {
    Bucket:'pmgv-files',
    Key:req.params.id
  };

  S3.getObject(opt,(err, data)=>{
     if(data){
        res.end(data.Body);
     }else{
       handleEntityNotFound(res);
     }
  });
}

// Creates a new Upload in the DB
export function create(req, res) {
  console.log('saving image ...')
  
  //console.log(req.body.file)
  if(req.body.base64 && req.body.name){

    var buf = new Buffer(req.body.file.replace(/^data:image\/\w+;base64,/, ""),'base64')
    var data = {
      Key: req.body.name, 
      Bucket:'pmgv-files',
      Body: buf,
      ContentEncoding: 'base64',
      ContentType: 'image/jpeg'
    };
    S3.putObject(data, function(err, data){
        if (err) { 
          console.log(err);
          console.log('Error uploading data: ', data); 
        } else {
          console.log('succesfully uploaded the image!');
        }
        return respondWithResult(res)({ok:true});
    });

  }

  //req.pipe(fs.createWriteStream("out_file.bin", { flags: 'w', encoding: null, fd: null}))
  //console.log(req)
  
  // create an incoming form object
  var form = new formidable.IncomingForm();

  // specify that we want to allow the user to upload multiple files in a single request
  //form.multiples = true;

  // store all uploads in the /uploads directory
  //form.uploadDir = path.join(__dirname, '/uploads');

  // every time a file has been uploaded successfully,
  // rename it to it's orignal name
  form.on('file', function(field, file) {
    if(!file.name){
      file.name = req.headers["file-name"];
    }
    var s3req = {
      Body: fs.readFileSync(file.path),
      Bucket:'pmgv-files',
      Key:file.name
    };

    S3.putObject(s3req,(err,data)=>{
      if(err) return respondWithResult(res)(err);
      return respondWithResult(res)(data);
    })
  });

  // log any errors that occur
  form.on('error', function(err) {
    console.log('An error has occured: \n' + err);
  });

  // once all the files have been uploaded, send a response to the client
  form.on('end', function() {
    //res.end('success');
  });

  // parse the incoming request containing the form data
  form.parse(req);
  /*function(err, fields, files) {
      res.writeHead(200, {'content-type': 'text/plain'});
    }
    */

}

// Updates an existing Upload in the DB
export function update(req, res) {
  if (req.body._id) {
    delete req.body._id;
  }
  return Upload.findById(req.params.id).exec()
    .then(handleEntityNotFound(res))
    .then(saveUpdates(req.body))
    .then(respondWithResult(res))
    .catch(handleError(res));
}

// Deletes a Upload from the DB
export function destroy(req, res) {
  var conf = {
    Bucket:'pmgv-files',
    Delete:{
      Objects:[
        {Key: req.params.id}
      ]
    }
  };
  S3.deleteObjects(conf, (err, data)=>{
    if(err) return res.json(err);

    return res.json({status:'OK'})
  });

}
